import { useEffect, useState } from "react";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  useAdmissionStudentUndertakingUpdate,
  useAdmissionStudentUpdateSectionChecklist,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../InstituteMember/InstituteStudent/InstituteStudent.module.css";

import Switch from "@mui/material/Switch";
import EditUndertakingAdmissionFormSection from "./FormSection/EditUndertakingAdmissionFormSection";
import BorderBottom from "../../../Ui/Border/BorderBottom";
const label = { inputProps: { "aria-label": "Switch demo" } };

const DynamicUndertakingCard = ({ st, onRefetch, applicationId, formId }) => {
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState("");
  const [visible, setVisible] = useState(false);
  const [add, setAdd] = useState(false);

  const [admissionStudentUndertakingUpdate] =
    useAdmissionStudentUndertakingUpdate();
  const [admissionStudentUpdateSectionChecklist] =
    useAdmissionStudentUpdateSectionChecklist();
  useEffect(() => {
    setState(st?.section_value);
  }, [st?.section_value]);

  useEffect(() => {
    setVisible(st?.section_visibilty);
  }, [st?.section_visibilty]);

  const onChange = (e) => {
    setState(e.target.value);
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onToggleChanged = (e) => {
    if (st?._id && formId) {
      setDisabled((pre) => !pre);
      admissionStudentUpdateSectionChecklist({
        formId: formId,
        bodyParameter: {
          section_name: st?.section_name,
          section_key: st?.section_key,
          section_visibilty: e.target.checked,
          fsid: st?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onUpdate = () => {
    if (applicationId && st?._id) {
      setDisabled((pre) => !pre);
      admissionStudentUndertakingUpdate({
        bodyParameter: {
          fsid: st?._id,
          flow: "APPLICATION",
          did: applicationId,
          content: state,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <section className={style.sfsc_card}>
        <div className={style.sfscc_outer}>
          <div className={style.sfscc_container}>
            <Switch {...label} checked={visible} onChange={onToggleChanged} />
            <span>{st?.section_name ?? ""}</span>
          </div>
          {visible ? (
            <CreateButton
              label="edit"
              customStyle={{
                paddingBlock: "0.2rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          ) : null}
        </div>

        <div className={style.sfsccc_inner}>
          <FormTextarea
            placeholder="Please type here"
            name={st?.section_key}
            type="text"
            value={state}
            onChange={onChange}
            customFormInputContainer={{
              width: "100%",
              marginBlock: "0.8rem",
            }}
          />
          <CreateButton
            label="update"
            customStyle={{
              paddingBlock: "0.2rem",
              margin: "0",
            }}
            onAction={onUpdate}
          />
        </div>
      </section>
      <BorderBottom />

      {add && (
        <EditUndertakingAdmissionFormSection
          onClose={onToggle}
          formId={formId}
          sectionId={st?._id}
          onRefetch={onRefetch}
          data={st}
        />
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default DynamicUndertakingCard;
