import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
import { useAdmissionStudentUpdateUndertakingSection } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
const EditUndertakingAdmissionFormSection = ({
  onClose,
  formId,
  onRefetch,
  sectionId,
  data,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    section_name: "",
    section_key: "",
    section_visibilty: true,
    section_type: "",
  });

  useEffect(() => {
    if (data) {
      setState({
        section_name: data?.section_name ?? "",
        section_key: data?.section_key ?? "",
        section_visibilty: data?.section_visibilty ?? true,
        section_type: data?.section_type ?? "",
      });
    }
  }, [data]);

  const [admissionStudentUpdateUndertakingSection] =
    useAdmissionStudentUpdateUndertakingSection();
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (val) => {
    setState((prev) => ({
      ...prev,
      section_type: val,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && formId && sectionId) {
      setDisabled((pre) => !pre);
      admissionStudentUpdateUndertakingSection({
        formId: formId,
        bodyParameter: {
          section_name: state.section_name,
          section_key: getUnderScoreFormat(state.section_name),
          section_visibilty: true,
          fsid: sectionId,
          section_type: state.section_type,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("update_undertaking_section")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <TileWrapper label="undertaking_type">
            {tileList?.map((tile) => (
              <TileCard
                key={tile?.key}
                tile={tile}
                onTile={onTile}
                activeTile={state?.section_type}
              />
            ))}
          </TileWrapper>
          <FormInput
            label={t("section_name")}
            placeholder={t("section_name_placeholder")}
            name={"section_name"}
            value={state.section_name}
            type={"text"}
            onChange={onChange}
            errorShow={errorField["section_name"]}
          />

          <CreateButton label="update" onAction={onAdd} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default EditUndertakingAdmissionFormSection;

const getUnderScoreFormat = (val) => {
  let st = val?.replaceAll(" ", "_");
  return st?.toLowerCase();
};
const tileList = [
  {
    key: "STUDENT",
    name: "student",
  },
  {
    key: "PARENTS",
    name: "parents",
  },
  {
    key: "OTHER",
    name: "other",
  },
];
